import { Component, OnInit } from '@angular/core';
import { AkunCashAdvanceService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Cash_advance, Pegawai_master } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private akunCashAdvanceService: AkunCashAdvanceService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) {}

    toogleUpload = false
    dataCashAdvance: Cash_advance = {}
    listPegawai: Pegawai_master[] = []

    id_cash_advance: string = ''

    ngOnInit() {
        this.id_cash_advance = this.activatedRoute.snapshot.paramMap.get('id_cash_advance')
        this.form()
        this.getlistPegawai()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'akutansi_cash_advance.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )

    }

    togleShowUpload() {
        this.toogleUpload = true;
    }

    getlistPegawai() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })
    }

    getData() {
        this.spinner.show()
        this.akunCashAdvanceService.getAkunCashAdvancesId(this.id_cash_advance).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "akutansi_cash_advance",
                    for_table: "erp_akutansi.cash_advance",
                    for_module_id: data.data.id_cash_advance
                })
            }
        )
    }

    dataForm: FormGroupTypeSafe<Cash_advance>
    form() {
        this.dataForm = this.fb.group<Cash_advance>({
            id_pegawai: new FormControl('', [Validators.required]),
            keterangan: new FormControl(''),
            nilai: new FormControl(''),
            tanggal_pengajuan: new FormControl(''),
            tanggal_pengembalian: new FormControl(''),
            keperluan: new FormControl(''),
            tipe_pengembalian: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_user: new FormControl(''),
            id_cash_advance: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            id_data_flow: new FormControl(''),
            nomor: new FormControl(''),
            status: new FormControl(''),
            posisi: new FormControl(''),
        })
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()

        this.dataCashAdvance = this.dataForm.value
        this.akunCashAdvanceService.putAkunCashAdvancesId({id: this.id_cash_advance, body: this.dataCashAdvance}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/ga/account/cash-advance/list'])
    }

}
