import { Cash_advance } from 'src/app/sdk/hr/models/cash-_advance';
import { Component, OnInit } from '@angular/core';
import { AkunCashAdvanceService, AkunBillingCategoryService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services';
import { Pegawai_master } from 'src/app/sdk/hr/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private akunCashAdvanceService: AkunCashAdvanceService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
    ) { }

    dataCashAdvance: Cash_advance = {}
    listPegawai: Pegawai_master[] = []

    id_billing: string = ''

    ngOnInit() {
        this.form()
        this.getlistPegawai()
    }

    getlistPegawai() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })
    }

    dataForm: FormGroupTypeSafe<Cash_advance>
    form() {
        this.dataForm = this.fb.group<Cash_advance>({
            id_pegawai: new FormControl('', [Validators.required]),
            keterangan: new FormControl(''),
            nilai: new FormControl(''),
            tanggal_pengajuan: new FormControl(''),
            tanggal_pengembalian: new FormControl(''),
            keperluan: new FormControl(''),
            tipe_pengembalian: new FormControl(''),
        })
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataCashAdvance = this.dataForm.value
        this.akunCashAdvanceService.postAkunCashAdvances(this.dataCashAdvance).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/ga/account/cash-advance/list'])
    }

}
