import { standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { Cash_advance } from 'src/app/sdk/hr/models/cash-_advance';
import { Component, OnInit } from '@angular/core';
import { AkunCashAdvanceService, DirektoriPegawaiMasterService, DirektoriVPegawaiPosisiService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { ActivatedRoute, Router } from '@angular/router';
import { Pegawai_master, V_pegawai_posisi } from 'src/app/sdk/hr/models';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';
import { StandarApprovalService } from 'src/app/components/approval/standar/standar-approval.service';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private akunCashAdvanceService: AkunCashAdvanceService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private activatedRoute: ActivatedRoute,
        private router: Router,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private toolsLogFlowService: ToolsLogFlowService,
        private standarApprovalService: StandarApprovalService,
    ) { }

    id_cash_advance: string = ''
    dataCashAdvance: Cash_advance = {}
    dataPegawai: Pegawai_master = {}
    dataPegawaiPosisi: V_pegawai_posisi = {}


    ngOnInit() {
        this.id_cash_advance = this.activatedRoute.snapshot.paramMap.get('id_cash_advance')
        this.getCashAdvance()
        this.getLog()

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getCashAdvance()
                    this.getLog()
                }
            }
        )


    }

    params: standarFlow = null;
    id_data_flow: string = ''

    getCashAdvance() {
        this.spinner.show()
        this.akunCashAdvanceService.getAkunCashAdvancesId(this.id_cash_advance).subscribe(
            data => {
                this.dataCashAdvance = data.data;
                this.getPegawai(data.data.id_pegawai);
                this.getPegawaiPosisi(data.data.id_pegawai);

                this.params = {
                    id: data.data.id_cash_advance,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_cash_advance"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )
    }

    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMastersId(id_pegawai).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }
    getPegawaiPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            data => {
                this.dataPegawaiPosisi = data.data;
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_cash_advance })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/ga/account/cash-advance/list'])
    }

}
